@font-face {
  font-family: 'icons';
  src: url('../fonts/icons.eot');
}
@font-face {
  font-family: 'icons';
  src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype');
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: 'icons';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-cross:before {
  content: "\e601";
}

.icon-tick:before {
  content: "\e602";
}

.icon-arrow-here:before {
  content: "\e600";
}

.icon-arrow-forward:before {
  content: "\e603";
}

.icon-arrow-back:before {
  content: "\e604";
}

.icon-warning:before {
  content: "\e605";
}

.icon-cog:before {
  content: "\e606";
}

.icon-zoom:before {
  content: "\e607";
}

.icon-volume-up:before {
  content: "\e608";
}